import { combineReducers } from 'redux';
import { handleActions } from 'redux-actions';
import * as actions from '../actions';

const requestStatus = handleActions({
  [actions.requestTickets]() {
    return 'requested';
  },
  [actions.requestSucsess]() {
    return 'sucsess';
  },
  [actions.requestFailure]() {
    return 'failure';
  },
}, 'none');

const cheapestTicketsFilter = handleActions({
  [actions.cheapestTickets](state, { payload }) {
    return !payload;
  },
}, false);

const fastestTicketsFilter = handleActions({
  [actions.fastestTickets](state, { payload }) {
    return !payload;
  },
}, false);

const oneStopFilter = handleActions({
  [actions.oneStopTickets](state, { payload }) {
    return !payload;
  },
}, false);

const twoStopsFilter = handleActions({
  [actions.twoStopsTickets](state, { payload }) {
    return !payload;
  },
}, false);

const threeStopsFilter = handleActions({
  [actions.threeStopsTickets](state, { payload }) {
    return !payload;
  },
}, false);

const withoutStopsFilter = handleActions({
  [actions.withoutStopsTickets](state, { payload }) {
    return !payload;
  },
}, false);

const tickets = handleActions({
  [actions.requestSucsess](state, { payload }) {
    return {
      ...state,
      all: payload.tickets,
      withoutStops: payload.tickets.filter(
        (item) => item.segments[0].stops.length === 0
        && item.segments[1].stops.length === 0,
      ).slice(0, 5),
      oneStop: payload.tickets.filter(
        (item) => item.segments[0].stops.length === 1
        && item.segments[1].stops.length === 1,
      ).slice(0, 5),
      twoStops: payload.tickets.filter(
        (item) => item.segments[0].stops.length === 2
        && item.segments[1].stops.length === 2,
      ).slice(0, 5),
      threeStops: payload.tickets.filter(
        (item) => item.segments[0].stops.length === 3
        && item.segments[1].stops.length === 3,
      ).slice(0, 5),
    };
  },
}, {
  all: [],
  withoutStops: [],
  oneStop: [],
  twoStops: [],
  threeStops: [],
});

const redusers = combineReducers({
  tickets,
  requestStatus,
  oneStopFilter,
  cheapestTicketsFilter,
  fastestTicketsFilter,
  twoStopsFilter,
  threeStopsFilter,
  withoutStopsFilter,
});

export default redusers;
